* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #ff7898;
  background-color: beige;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  font-size: 25px;
  font-weight: 500;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

a {
  color: #d34667;
  text-decoration: none;
}

a:visited {
  color: #d34667;
}

.navBttns {
  text-align: center;
  margin-top: 40vh;
}

li {
  margin-bottom: 25px;
  list-style: none;
}

.nav {
  text-align: center;
  background-color: #fafafa;
  border: 3px solid #d34667;
  border-radius: 15px;
  padding: 8px;
  transition: box-shadow .4s ease-in-out, color .4s ease-in-out;
}

.nav:hover {
  color: #fafafa;
  transition: background-color color 400ms;
  box-shadow: inset 0 0 40px 40px #ff7898;
}

